import React from "react"
import { useTodosState } from '../store/todo'

function TodoForm(){
	const { addTodo }  = useTodosState()
	return <input type="text" placeholder="Enter a new todo" onKeyUp={(e) => e.keyCode === 13 && (addTodo(e.target.value), e.target.value = "")} />
}

export default TodoForm