import React, { useEffect } from "react"
import { useRecoilValue } from 'recoil'
import { todosState, useTodosState } from '../store/todo'

function Todos(){
	const todos = useRecoilValue(todosState)
	const { updateTodo, saveTodos, clearTodos} = useTodosState()
	useEffect(() => {
		saveTodos()
	}, [todos])
	let content
	if(todos.length > 0){
		content = [todos.length > 0 && <p className="clear"><span onClick={clearTodos}>clear</span></p>, ...todos.map((todo, index) => <div className="todo" key={index} onClick={() => updateTodo(index)}>{ todo.title } <sub style={{color: todo.completed ? "#03A9F4" : "#4CAF50"}}>- { todo.completed ? "completed" : "pending" }</sub></div>)]
	}else{
		content = <p className="msg">Jobless ...</p>
	}
	return (
		<div class="todos">
			{ content }
		</div>
	)
}

export default Todos


