import { atom, useRecoilState } from 'recoil'

let prevData = localStorage.getItem('rtdata')
prevData = prevData && JSON.parse(prevData)

export let todosState = atom({
	key: 'todosState',
	default: prevData || []
})

export const useTodosState = () => {
	let [todos, setTodos] = useRecoilState(todosState)
	
	return {
		addTodo: todo => {
			if(!todo || !todo.trim()) return
			setTodos([{ title: todo, completed: false}, ...todos])
		},
		updateTodo: i => {
			let _todos = todos.map(todo => { return { ...todo }})
			_todos[i].completed = !_todos[i].completed
			setTodos(_todos)
		},
		saveTodos: () => {
			localStorage.setItem('rtdata', JSON.stringify(todos))
		},
		clearTodos: () => setTodos([])
	}
}